.button {
  @include fonts(14px, 16px);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  text-align: center;
  font-family: 'roboto-condensed-bold', Arial, sans-serif;
  width: 180px;
  border: 1px solid $sky-blue;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  opacity: 0.5;
}

.button:active {
  opacity: 0.3;
}
